<template>
  <div>
    <h1>用户详情界面</h1>
    <div>
      <Button
        type="primary"
        size="large"
        @click="click"
        style="width: 90%;margin-top:30px;"
      >
        退出登录
      </Button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { Button } from 'view-design';
import { Dialog } from 'vant';
Vue.component('Button', Button);

export default {
    name: 'UserDetail',
    data() {
        return {
            
        }
    },
    methods: {
        click(){
            localStorage.removeItem("Flag")
            Dialog.alert({
                title: '提示',
                message: '注销成功！',
            }).then(() => {
                this.$router.push('/login')
                // on close
            });
        },
    },
}
</script>

